//Backgrounds
@each $color,
$value in $theme-colors {
    @include bg-variant(".bg-#{$color}", $value);
}

.bg-white {
    background-color: $white !important;
}

//Colors
@each $color,
$value in $theme-colors {
    @include color-variant(".color-#{$color}", $value);
}

.color-white {
    color: $white !important;
}